\" */\n\n.details h1 {\n    margin: 20px;\n    text-align: center;\n    padding: 20px;\n    font-size: 24px;\n    background-color: #f0f8ff;\n}\n\n\n.grid {\n    display: grid;\n    grid-template-columns: 1fr 2fr; /* Two columns: buttons and content */\n    gap: 20px; /* Adjust the gap between columns */\n    align-items: flex-start; /* Align items at the top */\n}\n\n.buttons {\n    display: flex;\n    flex-direction: column;\n    align-items: flex-start; /* Buttons on the left side */\n}\n\n.dropdown-btn {\n    margin: 20px; /* Adjust the space between buttons */\n    cursor: pointer;\n    background-color: #4b0082; \n    color: white;\n    border: none;\n    padding: 10px;\n    border-radius: 5px;\n    height: 50px;\n    width: 100px;\n    text-align: center;\n    transition: background-color 0.3s ease;\n}\n\n.dropdown-btn:hover {\n    background-color: #6a5acd;\n}\n\n.dropdown-btn h2 {\n    margin-top: auto;\n    margin-bottom: auto;\n    text-align: center;\n}\n\n.content {\n    display: flex;\n    flex-direction: column;\n}\n\n.dropdown-content {\n    display: none;\n    padding-left: 20px; /* Adjust the space between button and content */\n}\n\n.dropdown-content h1{\n    margin-left: 0;\n}\n\n.dropdown-content.active {\n    display: block; /* Show content when active class is applied */\n    margin-top: -10px; /* Adjust for spacing */\n}\n\n.dropdown-content li {\n    list-style-type: circle;\n    margin: 5px 5px 5px 20px;\n    padding: 5px 50px 5px 5px;\n}\n\n.deleteButton{\n    border: none;\n    background-color: #00ffff;\n    /* color: white; */\n    font-size: medium;\n    cursor: pointer;\n    right: 25px;\n    position: absolute;\n    font-weight: bold;\n    transition: background-color 0.5s ease;\n}\n\n.deleteButton:hover{\n    background-color: red;\n}\n\n/* Styles for section with class \"subType\" */\n.subType{\n    background-color: #f0f8ff;\n}\n\n.subType h1 {\n    margin: 20px;\n    padding: 10px;\n    font-size: 24px;\n}\n\n.subType form label 